import {
	usergiftrecordPages,
	usergiftrecordAdd
} from 'api/api';
import giftListform from './businessComponents/giftListForm'
let pageData = {
	totalPrice: '',
	count: '',
	currencyUnit: '',

	itemIcon: '',
	itemName: '',
}
let scan = null;
export default {
	data() {
		return {
			giftData: [{
				ID: '123456',
				time: '2016-05-02',
				gift: '毛绒玩具'
			}],
			pageData: pageData,
			searchValue: '',
			tableData:'',
			currentPage: 1,
			pageSizes: [10, 20, 30],
			page: {
				start: 0,
				length: 10,
				draw: 1,
			},
			code:'',
			dialogVisible: false,
			dialogVisibleCode: false,
			inputValue: '',
			dialogTyle: '',
			showTips:false,
			showCode:true

		}
	},
	components: {
		giftListform
	},
	created() {
		if(!window.plus){
				this.showCode=false
			}
		this.init()
	},
	watch: {
		inputValue(t){
			if(t!=''){
				this.showTips=false
			}
		},
		searchValue(value) {
			if(!value) {
				this.init()
			}
		},
		dialogVisibleCode(f){
			if(!f&&scan!=null){
				scan.close()
				scan=null
			}
		}
	},
	methods: {
		getQrcode(){
			
				if(this.inputValue==''||!/^[0-9]{1,2}?$/.test(this.inputValue)){
					return this.showTips=true
				}
				if(scan){
					scan.start()
				}else {
					document.addEventListener('plusready',this.getScan())
					
				}
			
			

		},
		getScan(){
			 
				scan = new plus.barcode.Barcode('bcid',{},{background:'#000000'});
					scan.onmarked = ( type,result,file)=>{
										var text = '未知: ';
										switch(type){
											case plus.barcode.QR:
											text = 'QR: ';
											break;
											case plus.barcode.EAN13:
											text = 'EAN13: ';
											break;
											case plus.barcode.EAN8:
											text = 'EAN8: ';
											break;
										}
										this.getCode(result)
									}
					 
					scan.start();

		},
		getCode(code){
			scan.close();
			scan=null
			this.code=code
			usergiftrecordAdd({userId:this.$store.state.user.shopUserId,quantity:this.inputValue,QRCode:code}).then(res => {
					
					 this.init()
				}).catch(err=>{
					 
				})
		},
		showDilog(type, data = typeFormData) {

			this.pageData = { ...data,...data.appUsersInfo,...data.game,...data.gameGiftInfo
			}

			this.dialogVisible = true
			this.dialogTyle = type
		},
		verification(){
			if(!window.plus){
				this.$notify.warning({
		          title: this.$t('commom.alertTitle'),
		          message:  this.$t('commom.phoneType'),
		          duration:3000
		        });
			}else{
				this.dialogVisibleCode=true
			}
			
		},
		init() {
			//busItemName
			usergiftrecordPages(this.page).then(res => {
				this.tableData =res.data.obj
					
			})
		},
		onQuery() {
			if(this.searchValue) {
				usergiftrecordPages({ ...this.page,
					gameName: this.searchValue
				}).then(res => {
					this.tableData =res.data.obj
				})
			}
		},
		getpageData() {
			usergiftrecordPages({ ...this.page,
				busItemName: this.searchValue
			}).then(res => {
				this.tableData = { ...res.data.obj,
					...res.data.obj.busiItem
				}
			})
		},
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
			this.getpageData()
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(() => {
				this.getpageData()
			}, 0)
		},
	},

}



